.weather-forecast {
    background: #fff;
    border: 1px solid;
    margin: 30px auto;
    width: 100%;
    max-width: 1100px;
    hieght: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    position: relative;
    font-size: 14px;
}

.weather-forecast .day {
    border: 1px solid;
    width: 100%;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    height: 150px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.weather-forecast .hour-weather {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    border: 1px solid;
    height: 150px;
    width: 100%;
    -ms-flex-positive: 1;
        flex-grow: 1;
    position: relative;
}

.weather-forecast .weather-icon {
    width: 80px;
    height: auto;
}

.weather-forecast .day .date {
    position: absolute;
    top: 5px;
    left: 5px;
}

.weather-forecast .day .hour {
    position: absolute;
    top: 5px;
    right: 5px;
}

.weather-forecast .day .weather-desc {
    margin-top: 20px;
    margin-bottom: -20px;
}


